* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* All the snapping stuff */
.scroll-container {
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-snap-type: y mandatory;
  position: relative;
}

.section-container {
  height: 100vh;
  scroll-snap-align: center;
}

.section-container-footer {
  height: 50vh;

  scroll-snap-align: bottom;
}
